	module.exports = {"pathContext":{"docHtml":"<h1>is-ip <a href=\"https://travis-ci.org/sindresorhus/is-ip\"><img src=\"https://travis-ci.org/sindresorhus/is-ip.svg?branch=master\" alt=\"Build Status\"></a></h1>\n<blockquote>\n<p>Check if a string is an IP address</p>\n</blockquote>\n<h2>Install</h2>\n<pre><code class=\"language-sh\">$ npm install --save is-ip\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">var isIp = require('is-ip');\n\nisIp('192.168.0.1');\n//=> true\n\nisIp('1:2:3:4:5:6:7:8');\n//=> true\n\nisIp.v4('1:2:3:4:5:6:7:8');\n//=> false\n</code></pre>\n<h2>API</h2>\n<h3>isIp(string)</h3>\n<p>Check if a string is IPv4 or IPv6.</p>\n<h3>isIp.v4(string)</h3>\n<p>Check if a string is IPv4.</p>\n<h3>isIp.v6(string)</h3>\n<p>Check if a string is IPv6.</p>\n<h2>License</h2>\n<p>MIT © <a href=\"http://sindresorhus.com\">Sindre Sorhus</a></p>","docPath":"argo-ci/node_modules/is-ip/readme","proj":"argo-ci"}}

